.main {
  position: relative;
}

.input_container {
  position: fixed;
  top: 0;
  background-color: #222a33;
  color: white;
  width: 100%;
  padding-bottom: 10px;
  z-index: 2;
}

.input_container :nth-child(2) {
  margin-left: 50px;
  padding: 10px;
  font-size: large;
}

.input_container button {
  margin-left: 10px;
  padding: 10px;
  font-size: large;
  background-color: #1cb397;
  color: white;
  border-radius: 5px;
  font-weight: bolder;
  border: none;
  cursor: pointer;
}

.input_container h1 {
  margin-left: 50px;
}

.flexContainer {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.flexContainer div {
  margin-bottom: 20px;
}
.flexContainer div:hover {
  transform: scale(1.1, 1.1);
  transition-duration: 0.2s;
  z-index: 1;
}
.photo_container {
  margin: 100px;
  margin-top: 200px;
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.photo_container div {
  margin: 10px;
}

.details {
  position: absolute;
  bottom: 0;
  left: 5%;
  opacity: 0;
  color: white;
}
.details a {
  text-decoration: none;
  color: white;
}

.details:hover {
  opacity: 1;
}

.img {
  position: relative;
}
