.main {
  position: relative;
}
.video_container {
  display: grid;
  justify-content: center;
  grid-template-columns: auto auto auto;
  column-gap: 50px;
  row-gap: 50px;
  margin-top: 200px;
}

.input_container {
  position: fixed;
  top: 0;
  background-color: #222a33;
  color: white;
  width: 100%;
  padding-bottom: 10px;
  z-index: 1;
}

.input_container :nth-child(2) {
  margin-left: 50px;
  padding: 10px;
  font-size: large;
}

.input_container button {
  margin-left: 10px;
  padding: 10px;
  font-size: large;
  background-color: #1cb397;
  color: white;
  border-radius: 5px;
  font-weight: bolder;
  border: none;
  cursor: pointer;
}

.input_container h1 {
  margin-left: 50px;
}
.video_container div {
  position: relative;
  height: fit-content;
}
.img {
  box-shadow: 5px 5px 25px grey;
  border-radius: 10px;
}
.video_container div:hover {
  transform: scale(1.2, 1.2);
}
.logo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
