.btn {
  text-align: center;
  font-size: xx-large;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  border-radius: 10px;
  cursor: pointer;
  padding: 30px;
  margin: 15px;
  width: 30%;
}
.flexBox {
  display: flex;
  margin-left: 50%;
  transform: translateX(-50%);
}

.btn:hover {
  background-color: black;
}

.main {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("./../public/img.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  justify-content: center;
}

.main p {
  font-size: 80px;
  font-weight: bold;
  line-height: 100px;
}
